@import url(https://fonts.googleapis.com/css2?family=Amatic+SC);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: azure;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a, .nav-item a:visited, a:focus {
  text-decoration: underline;
  color: black;
}

p.page_desc {
  
  font-weight: 500;
}
.Nav-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 5%;
  margin: auto;
  height: 2em;
  font-family: 'Amatic SC', cursive;
}

.Nav-bar a {
  text-decoration: none;
}

.Nav-bar .nav-menu {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 80%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.Nav-bar .nav-item h3 {
  margin-left: .5em;
  margin-right: 3em;
  font-size: 1.5em;
  font-weight: 100;
}

.nav-item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 650px) {
  .nav-item {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }

  .Nav-bar .nav-item h3 {
    margin-left: .1em;
    margin-right: 1em;
    font-size: 1.2em;
    font-weight: 100;
  }
  
  .Nav-bar .nav-menu {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 80%;
  }

  .a_btn {
    text-decoration: none;
    background-color: #EEEEEE;
    padding: 2px 6px 2px 6px;
    border-top: 1px solid #CCCCCC;
    border-right: 1px solid #333333;
    border-bottom: 1px solid #333333;
    border-left: 1px solid #CCCCCC;
    border-radius: 5px;
  }
}

.svg-inline--fa {
  height: 5em
}

.nav-item .fa-home {
  font-size: 1.5em;
}

.fa-home {
  color: #28a386
}

.nav-item * {
  cursor: pointer;
}
.App {
  text-align: center;
  margin: auto;
  margin-top: 20%;
  width: 60%;
  padding: 10px;
  font-size: 1.4em;
}

.App > * {
  margin-bottom: .2%;
}

.App p {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 1.3em;
  font-weight: 400;
}

.App .name {
  font-family: 'Amatic SC', cursive;
  font-size: 1.5em;
  font-style: italic;
}

.social-holders {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70%;
  margin: auto;
}

.svg-inline--fa {
  height: 2em;
}

@media screen and (max-width: 700px) {
  .App {
    width: 80%;
    margin-top: 45%;
  }
  
  .App p {
    font-size: 5vw;
    margin-bottom: 10%;
  }

  .App .home_break {
    display: block;
    text-align: center;
  }
}
.a-project {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 25em;
  margin-bottom: 2em;
  padding-top: 2em;
  padding-bottom: 2em;
}

.a-project:nth-child(odd) {
  flex-direction: row;
}

.a-project:nth-child(even) {
  flex-direction: row-reverse;
}

.a-project img {
  max-width: 40%;
  height: 100%;
  margin: 2%;
  border-radius: 1em;
}

.a-project .a-project-footer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 40%;
  height: 100%;
  padding: .75rem 1rem;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
}

.project_title {
  font-size: 1.2em;
}

.project_desc {
  font-size: 1.5vw; 
}

@media screen and (max-width: 750px) {
  .a-project {
    height: 15em;
    margin-bottom: 1em;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (max-width: 650px) {
  .a-project {
    height: 15em;
    margin-bottom: 1em;
    margin-left: auto;
    margin-right: auto;
  }

  .project_title {
    font-size: 1em;
  }
  
  .project_desc {
    font-size: .9em;
  }
}
.all-projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff;
}

.all-projects > span {
  text-align: left;
  align-self: flex-start;
}

.all-projects div {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.page-items {
  margin-top: 5%;
}

.page-items > div:nth-child(odd) {
  background: #fff;
}

.page-items > div:nth-child(even) {
  background: #003c3c;
}

.page-items > div:last-child {
  margin-bottom: 0;
}

@media screen and (max-width: 650px) {
  .all-projects div {
    display: flex;
    flex-direction: column;
  }
}
.about_me {
  display: flex;
  flex-wrap: wrap;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.about_me > .nav {
  width: 100%;
}

.about_me h2{
  font-family: 'Amatic SC', cursive;
}

.about_me div {
  width: 50%;
  font-size: 2.5vh;
}

.about_me > .write_up {
  width: 48%;
  text-align: center;
  padding-left: 1%;
  padding-right: 1%;
  padding-bottom: .5%;
}

.about_me img {
  width: 100%;
  height: 100%;
}

.about_me > .mail-form > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.about_me > .mail-form input, .about_me > .mail-form textarea {
  width: 80%;
  border-radius: .5em;
  outline: none;
}

.mail-form textarea {
  resize: none;
}

.mail-form a {
  font: bold 11px Arial;
  text-decoration: none;
  background-color: #EEEEEE;
  color: #333333;
  padding: 2px 6px 2px 6px;
  border-top: 1px solid #CCCCCC;
  border-right: 1px solid #333333;
  border-bottom: 1px solid #333333;
  border-left: 1px solid #CCCCCC;
}

.mail-form p {
  text-align: center;
  font-size: 1.2em;
}

@media screen and (max-width: 700px) {
  .about_me div {
    width: 100%;
    font-size: 2.5vw;
  }

  .about_me > .write_up {
    width: 100%;
  }

  .about_me img {
    width: 100%;
  }
  
  .about_me > .mail-form > div {
    margin-bottom: 2em;
  }
}
